import React, { useState } from 'react';
import SingleColor from './SingleColor';
import Values from 'values.js';
import './scss/App.scss';
import { toast } from 'react-toastify';
import "react-toastify/dist/ReactToastify.css";

toast.configure();

function App() {
  const [color, setColor] = useState('');
  const [error, setError] = useState(false);
  const [list, setList] = useState(new Values('#3f37c9').all(20));

  const handleSubmit = (e) => {
    e.preventDefault()
    try {
      let colors = new Values(color).all(20)
      setList(colors)
    } catch (error) {
      setError(true)
      console.log(error); 
    }
  }

  const searchNotify = () => {
    toast.info('Click color to copy!', {
      position: toast.POSITION.BOTTOM_CENTER,
      autoClose: 2000,
      hideProgressBar: true,
      closeOnClick: false,
      pauseOnHover: false,})
  }

  return (
    <>
    <section className="container">
      <h1>color generator</h1>

      <form onSubmit={handleSubmit}>
        <input type="text" value={color} onChange={(e)=> setColor(e.target.value)} placeholder="#29F48F" className={`${error?'error': null}`}/>        
        <button className="btn fas fa-search" type="submit" onClick={searchNotify}></button>
      </form>
    </section>

    <section className='colors'>
        {list.map((color, index) => {
          return (
            <SingleColor
              key={index}
              {...color}
              index={index}
              hexColor={color.hex}
            />
          )
        })}
      </section>
    </>
  );
}

export default App;
